/* eslint-disable */
import './src/styles/reset.sass'
import './src/styles/util.sass'
import './src/styles/font.sass'
import './src/styles/site.sass'
import './src/styles/prism-vscodedark.css'
import './src/styles/markdown-content.sass'
import './src/styles/highlight.sass'
/* eslint-enable */

document.body.addEventListener('click', (e) => {
  const { target } = e

  if (!target) {
    return
  }

  if (!target.matches('.gatsby-resp-image-image')) {
    return
  }

  e.preventDefault()
})
